import React from 'react';
import {ScrollView, Text} from 'react-native';
import {useRoute} from '@react-navigation/native';
import {RouteProps} from '@/navigations';
import {Header, Screen} from '@/components';
import useFetchTodo from './apis/useFetchTodo';

export type ClaimDetailProps = {id: number};

const ClaimDetailScreen: React.FC<unknown> = () => {
  const route = useRoute<RouteProps<'claimDetail'>>();
  const {data} = useFetchTodo(route.params.id);

  return (
    <>
      <Header dark title="Claim detail" />
      <Screen style={{paddingLeft: 16, paddingRight: 16}}>
        <ScrollView>
          <Text>{data?.title}</Text>
        </ScrollView>
      </Screen>
    </>
  );
};

export default ClaimDetailScreen;
