import {isEmpty as _isEmpty} from 'lodash';

export const getUniqueNumber = (() => {
  let i = 0;
  return () => {
    i++;
    return i;
  };
})();

export function isEmpty(value: number | string | boolean | unknown) {
  if (typeof value === 'number') {
    return false;
  }

  if (typeof value === 'boolean') {
    return false;
  }

  if (value instanceof Date) {
    return false;
  }

  return _isEmpty(value);
}

export function isHasValue(value: number | string | boolean | unknown) {
  return !isEmpty(value)!;
}
