import {queryOptions, useQuery} from '@tanstack/react-query';
import placeholderService from '@/services/placeholder';
import type {QueryConfig} from '@/types/libraries';

const fetchTodoListQueryOptions = () => {
  return queryOptions({
    queryKey: placeholderService.queryKeys.getTodoList,
    queryFn: () => placeholderService.fetchTodoList(),
  });
};

const useFetchTodoList = (
  queryOptions?: QueryConfig<typeof fetchTodoListQueryOptions>,
) => {
  return useQuery({
    ...fetchTodoListQueryOptions(),
    ...queryOptions,
  });
};

const fetchTodoQueryOptions = (id: number) => {
  return queryOptions({
    queryKey: [...placeholderService.queryKeys.getTodo, id],
    queryFn: () => placeholderService.fetchTodo(id),
  });
};

const useFetchTodo = (
  id: number,
  queryOptions?: QueryConfig<typeof fetchTodoQueryOptions>,
) => {
  return useQuery({
    ...fetchTodoQueryOptions(id),
    ...queryOptions,
  });
};

export default {useFetchTodoList, useFetchTodo};
