import {useEffect} from 'react';
import {placeholderQuery} from '@/hooks/apis';
import {hideLoading, showLoading} from '@/components/GlobalLoading';

const useFetchTodoList = () => {
  const {isFetching, data} = placeholderQuery.useFetchTodoList({});

  useEffect(() => {
    isFetching ? showLoading() : hideLoading();
  }, [isFetching]);

  return {
    data,
  };
};

export default useFetchTodoList;
