import {PlaceServiceType} from '@/types/apis/placeholder';
import axios, {AxiosResponse} from 'axios';

class PlaceholderService {
  queryKeys = {
    getTodoList: ['placeholder', '/todos'],
    getTodo: ['placeholder', '/todos'],
  };

  public fetchTodoList = () =>
    axios
      .get<any, AxiosResponse<PlaceServiceType.IFetchTodoListResponse>>(
        'https://jsonplaceholder.typicode.com/todos',
      )
      .then(res => res.data);

  public fetchTodo = (id: number) =>
    axios
      .get<any, AxiosResponse<PlaceServiceType.IFetchTodoResponse>>(
        `https://jsonplaceholder.typicode.com/todos/${id}`,
      )
      .then(res => res.data);
}

export default new PlaceholderService();
