import {useEffect} from 'react';
import {placeholderQuery} from '@/hooks/apis';
import {hideLoading, showLoading} from '@/components/GlobalLoading';

const useFetchTodo = (id: number) => {
  const {isFetching, data} = placeholderQuery.useFetchTodo(id);

  useEffect(() => {
    isFetching ? showLoading() : hideLoading();
  }, [isFetching]);

  return {
    data,
  };
};

export default useFetchTodo;
