import React, {ComponentProps} from 'react';
import {
  NavigationContainer,
  NavigatorScreenParams,
  RouteProp,
} from '@react-navigation/native';
import {
  createNativeStackNavigator,
  NativeStackNavigationProp,
} from '@react-navigation/native-stack';
import claimDetailNavigation, {ClaimDetailNavigationProps} from './ClaimDetail';
import claimListNavigation, {ClaimListNavigationProps} from './ClaimList';

type AppStackProps = {
  claimApp: NavigatorScreenParams<MiniAppStackProps>;
};

export type RouteProps<RouteName extends keyof MiniAppStackProps> = RouteProp<
  MiniAppStackProps,
  RouteName
>;

export type AppNativeStackNavigation = NativeStackNavigationProp<AppStackProps>;

const Stack = createNativeStackNavigator<AppStackProps>();

const AppStack = () => {
  return (
    <Stack.Navigator
      initialRouteName="claimApp"
      screenOptions={{headerShown: false}}>
      <Stack.Screen name="claimApp" component={MiniStackNavigator} />
    </Stack.Navigator>
  );
};

// mini app
export type MiniAppStackProps = ClaimListNavigationProps &
  ClaimDetailNavigationProps;

const MiniStack = createNativeStackNavigator<MiniAppStackProps>();
export type MiniStackProps = ComponentProps<typeof MiniStack.Screen> & {
  id: number;
};

const navigations = [claimListNavigation, claimDetailNavigation];

const MiniStackNavigator = () => {
  return (
    <MiniStack.Navigator
      initialRouteName="claimList"
      screenOptions={{headerShown: false}}>
      {navigations.map(item => (
        <MiniStack.Screen key={item.id} {...item} />
      ))}
    </MiniStack.Navigator>
  );
};

export const AppNavigation = () => {
  return (
    <NavigationContainer>
      <AppStack />
    </NavigationContainer>
  );
};

const WrapMiniNavigator = () => {
  return <MiniStackNavigator />;
};

export default WrapMiniNavigator;
